<template>
  <div>
    <div class="cell" @click="onClickSelf">
      <template v-if="a">{{text}}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
import { log } from "util";
export default {
  props: ["n", "finished"],
  components: {},
  data() {
    return {
      a: false,
      text: ""
    };
  },
  // computed: {
  //   text() {
  //     return this.n % 2 == 0 ? "x" : "o";
  //   }
  // },
  methods: {
    onClickSelf() {
      if (this.text !== "") {
        return;
      }
      if (this.finished) {
        return;
      }
      this.a = true;
      this.text = this.n % 2 === 0 ? "x" : "o";
      this.$emit("click", this.text);
    }
  }
};
</script>

<style>
.cell {
  border: 1px solid black;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80px;
}
</style>
